$white: #fff;
$Dwhite: #FAFAFA;
$black: #000;
$Dgray: #282630;
$grey: #65636A;
$Lgrey: #8B8A91;
$Lyellow: #FEE382;
$Dgrey: #FFDD63;
$boxShadow: 24px 40px 32px rgba(0, 0, 0, 0.12);

// font-family: 'Cabin', sans-serif
// font-family: 'Poppins', sans-serif

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Cabin', sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 14px;
  line-height: 150%;
  color: $Dgray; }

a {
  text-decoration: none; }

body {
  background: $white;
  min-height: 100vh; }

section {
  max-width: 100%;
  padding: 0 60px; }

.container {
  width: 100%;
  height: 100%;
  margin: 0 auto; }

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Poppins', sans-serif;
  letter-spacing: 0.03em;
  width: max-content;
  line-height: 21px;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  padding: 10px 20px;
  background: $Lyellow;
  border-radius: 25px; }

//---Header
header {
  z-index: 999;
  position: fixed;
  width: 100%;
  padding: 0 60px;
  padding-top: 30px;
  .container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    .logo {
      height: 72px;
      justify-self: flex-start;
      margin-right: auto;
      img {
        height: 100%;
        width: 100%; } }
    .menu {
      ul {
        display: flex;
        list-style: none;
        align-items: center;
        li {
          margin-left: 56px; } } }
    .menu-btn {
      margin-left: 110px;
      ul {
        display: flex;
        list-style: none;
        align-items: center;
        li {
          height: 100%;
          margin-left: 40px;
          img {
            height: 100%;
            width: auto; } } } } } }

.main {
  min-height: 950px;
  height: 100vh;
  .container {
    position: relative;
    height: 950px;
    background: url(../img/BG1.png);
    background-repeat: no-repeat;
    background-position-x: left;
    background-position-y: center;
    display: flex;
    justify-content: space-between;
    .social {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      margin-bottom: 80px;
      a {
        margin-top: 60px; } }
    .main-content {
      width: 40%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      .main-title {
        font-family: 'Poppins', sans-serif;
        font-weight: 600;
        font-size: 96px;
        margin-bottom: 50px;
        line-height: 144px;
        display: flex;
        flex-direction: column;
        &::after {
          content: " ";
          display: block;
          width: 116px;
          height: 3px;
          background: $Dgray; } }
      .main-text {
        font-weight: 600;
        font-size: 22px;
        color: $grey;
        margin-bottom: 50px; }
      .button-wrapper {
        display: flex;
        .btn {
          margin-right: 25px; } } }
    .arrow-down {
      position: absolute;
      bottom: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      right: 0px;
      width: 94px;
      height: 100px;
      background: $Dwhite;
      border-top-left-radius: 50%;
      border-top-right-radius: 50%;
      cursor: pointer; } } }

.dishes {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  .container {
    display: flex; }
  .main-dishes {
    padding-top: 80px;
    padding-bottom: 60px;
    padding-left: 40px;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
    max-width: 500px;
    .dishes-img {
      img {
        max-width: 100%;
        height: auto; } }
    .dishes-title {
      text-align: center;
      margin-bottom: 20px;
      font-family: 'Poppins', sans-serif;
      font-weight: 600;
      font-size: 36px;
      line-height: 54px; }
    .dishes-text {
      width: 80%;
      margin: 0 auto;
      margin-bottom: 30px;
      color: $grey;
      font-size: 16px; }
    .btn-wrapper {
      display: flex;
      justify-content: center;
      .btn {
        margin: 0 15px; } } }
  .dishes-wrapper {
    .dishes-title {
      font-family: 'Poppins', sans-serif;
      font-weight: 600;
      font-size: 48px;
      line-height: 72px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 95px;
      margin-bottom: 50px;
      &::after {
        content: " ";
        display: block;
        width: 116px;
        height: 3px;
        background: $Dgray; } }
    .wrapper {
      display: flex;
      .dishes-item {
        width: 33.3%;
        max-width: 300px;
        margin: 0 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .dishes-img {
          img {
            width: 100%;
            height: auto; } } } } }
  .top {
    z-index: -1;
    position: absolute;
    height: 40%;
    width: 100%;
    top: 0px;
    left: 0px;
    background: $Dwhite; } }
